.countries-by-letter-list {
  @include list-reset;

  position: absolute;

  display: none;
  margin: 0;

  &--selected {
    left: 0;
    bottom: -425px;

    display: block;
  }
}

.countries-by-letter-list__link {
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 1.38;
  letter-spacing: normal;
  color: $brown-gray;

  &:hover,
  &:focus {
    color: $middle-dark-blue;
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: $tablet-width) {
  .countries-by-letter-list {
    &--selected {
      left: 470px;
      bottom: -220px;
    }
  }

  .countries-by-letter-list__item {
    margin-bottom: 5px;
  }

  .countries-by-letter-list__link {
    font-size: 20px;
    line-height: 1.5;
  }
}

@media (min-width: $desktop-width) {
  .countries-by-letter-list {
    position: static;

    display: block;
    margin: 25px 0 80px 86px;
  }

  .countries-by-letter-list__item {
    margin-bottom: 0;
  }
}
