.countries-list {
  @include list-reset;
}

.countries-list__item {
  margin-bottom: 8px;
}

.countries-list__item:last-child .countries-list__description {
  background: linear-gradient(to bottom, $pure-white 0%, $middle-gradient 60%, $middle-white 100%);
}

.countries-list__image {
  border-radius: 10px 10px 0 0;
}

@media (min-width: $tablet-width) {
  .countries-list__item {
    display: flex;
    flex-direction: row-reverse;
    height: 140px;
    margin-bottom: 10px;
  }

  .countries-list__item:last-child .countries-list__description {
    background: $pure-white none;
  }

  .countries-list__image {
    height: 100%;
    width: 185px;
    min-width: 185px;

    border-radius: 0 20px 20px 0;
  }
}

@media (min-width: $desktop-width) {
  .countries-list {
    margin: -280px 0 0 70px;
    max-width: 560px;
  }

  .countries-list__item:last-child {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      z-index: 2;

      width: 100%;
      height: 100%;

      background: linear-gradient(to bottom, rgba($middle-white, 0) 0%, rgba($middle-white, 0) 50%, $middle-white 100%);
    }
  }

  .countries-list__image {
    min-width: 185px;
  }
}
