.companions-filter {
  padding: 25px 25px 20px 25px;
  margin-bottom: 25px;

  background-color: $yellow;
  border-radius: 20px;
}

.companions-filter__tagline {
  display: block;
  margin-bottom: 20px;

  font-size: 22.5px;
  font-weight: 700;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: $middle-dark-blue;
}

@media (min-width: $tablet-width) {
  .companions-filter {
    padding: 35px 35px 40px 35px;
    margin-bottom: 40px;
  }

  .companions-filter__tagline {
    margin-bottom: 35px;

    font-size: 30px;
  }
}

@media (min-width: $desktop-width) {
  .companions-filter {
    position: absolute;
    z-index: 1;
    right: 60px;

    padding: 50px 45px 50px 50px;
    margin-bottom: 0;
    width: 285px;
  }

  .companions-filter__tagline {
    max-width: 160px;
    margin-bottom: 20px;

    font-size: 30px;
    line-height: 0.98;
  }
}
