.about-service {
  position: relative;
  z-index: 6;

  width: 100%;
  margin: 0 auto;

  background-color: $dark-blue;
  border-radius: 0 0 30px 30px;
}

.about-service__wrapper {
  width: 320px;
  margin: 0 auto;
  padding: 0 60px 85px 50px;

  background-image: url("../img/bg/about-mobile@1x.png");
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 0 0 40px 40px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg/about-mobile@2x.png");
    background-size: 90%;
  }
}

.about-service__header {
  margin: 0;
  padding: 30px 0 0 0;

  font-size: 24px;
  font-weight: 700;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: $orange;
}

.about-service__text {
  font-size: 16px;
  font-weight: 300;
  font-style: normal;
  line-height: 1.38;
  letter-spacing: normal;
  color: $pure-white;
}

.about-service__advice {
  font-size: 16px;
  font-weight: 700;
  font-style: normal;
  line-height: 1.38;
  letter-spacing: normal;
  color: $pure-white;
}

.about-service__btn {
  display: none;
}

@media (min-width: $tablet-width) {
  .about-service {
    padding-top: 110px;

    background-color: $dark-blue;
    border-radius: 0 0 60px 60px;
  }

  .about-service__wrapper {
    width: 768px;
    padding: 0 395px 90px 75px;

    background-image: url("../img/bg/about-tablet@1x.png");
    background-repeat: no-repeat;
    background-position: 385px 35px;
    border-radius: 0 0 60px 60px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg/about-tablet@2x.png");
      background-size: 50%;
    }
  }

  .about-service__header {
    font-size: 40px;
  }

  .about-service__text {
    width: 300px;
    margin: 40px 0 35px 0;

    font-size: 28px;
    line-height: 1.18;
  }

  .about-service__advice {
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 50px;

    font-size: 21px;
    line-height: 1.19;
  }

  .about-service__btn {
    display: block;
    max-width: 275px;
  }
}

@media (min-width: $desktop-width) {
  .about-service {
    margin-top: 0;
    padding-top: 60px;

    border-radius: 0 0 60px 60px;
  }

  .about-service__wrapper {
    width: 1440px;
    min-height: auto;
    margin: 0 auto;
    padding: 40px 0 165px 130px;

    background-image: url("../img/bg/about-desktop@1x.png");
    background-repeat: no-repeat;
    background-position: 750px bottom;
    border-radius: 0 0 60px 60px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg/about-desktop@2x.png");
      background-size: 45%;
    }
  }

  .about-service__header {
    position: relative;

    font-size: 45px;

    &::before {
      content: "";
      position: absolute;
      top: -30px;
      left: -70px;
      z-index: -1;

      width: 213px;
      height: 213px;

      background-color: $dark-bg-blue;
      border-radius: 20px;
    }
  }

  .about-service__text {
    width: auto;
    max-width: 620px;
    margin-top: 60px;
    margin-bottom: 35px;
  }

  .about-service__advice {
    max-width: 410px;
    margin-bottom: 85px;
  }
}
